import React from "react";
import { useContext } from "react";
import CartContext from "../../store/cart-context";
import "./Cart.css";
import Modal from "../UI/Modal";
import CartItem from "./CartItem";

const Cart = (props) => {
  const cartCtx = useContext(CartContext);
  const hasItems = cartCtx.items.length > 0;
  const cartItemRemoveHandler = (id) => {
    cartCtx.removeItem(id)
  };
  const cartItemAddHandler = (item) => {
    cartCtx.addItem({...item,amount:1})
  };
  const cartItemDeleteHandler=(id)=>{
    cartCtx.deleteItem({id})
  }
  const cartItems = (
    <ul class="cart-row">
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          imageUrl={item.imageUrl}
          title={item.title}
          price={item.price}
          amount={item.amount}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
          onDelete={cartItemDeleteHandler.bind(null,item)}
        />
      ))}
    </ul>
  );

  return (
   
      <Modal onClose={props.onClose}>
        <button class="cancel" onClick={props.onClose}>
          X
        </button>
        <h2 class="cart-header">CART</h2>

        <div class="cart-row cart-header">
          <span class="title">ITEM</span>
          <span class="price">PRICE</span>
          <span class="quantity">QUANTITY</span>
        </div>
        <li class="cart-items">{cartItems}</li>

        <div class="cart-total">
          <span>
            <span class="total-title">
            
              <strong>Total</strong>
            </span>
            Rs.<span id="total-value">{`${cartCtx.totalAmount}`}</span>
          </span>
        </div>
        {hasItems && (
          <button class="purchase-btn" type="button">
            PURCHASE
          </button>
        )}
      </Modal>
 
  );
};

export default Cart;
